import React from "react"
import { graphql } from "gatsby"
import { Translation } from "react-i18next"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import GetInTouch from "../../components/forms/get-in-touch"
import HeroHalf from "../../components/heros/hero-half"
import i18n from "../../lib/i18n"

export const query = graphql`
  query {
    wpLocation(title: { eq: "Houston" }) {
      id
      title
      locationFields {
        title
        city
        streetAddress
        zipCode
        state
        phone
        email
        descriptionEnglish
        descriptionSpanish
        reviews
        image {
          sourceUrl
        }
      }
    }
  }
`

const Locations = ({ data }) => {
  const location = data.wpLocation.locationFields

  const {
    title,
    city,
    streetAddress,
    zipCode,
    state,
    phone,
    email,
    descriptionEnglish,
    descriptionSpanish,
    image,
    reviews,
  } = location
  return (
    <Layout>
      {i18n.language === "en" ? (
              <SEO title="Houston HVAC Company | HVAC Contractors in Houston" />
            ) : (
              <SEO title="Compañía de HVAC en Houston | Contratadores de HVAC en Houston" />
            )}
      <Translation>
        {t => (
          <HeroHalf
            headline="Air Rey"
            subheadline={city}
            body={
              i18n.language === "en"
                ? `${descriptionEnglish}`
                : `${descriptionSpanish}`
            }
            image={image.sourceUrl}
            button1Text={phone}
            button1Link={`tel:${phone}`}
            button2Text={i18n.language === "en" ? `Contact Us` : `Contáctenos`}
            button2Link="#getInTouch"
          />
        )}
      </Translation>
      <GetInTouch
        locationName={title}
        address={streetAddress}
        city={city}
        state={state}
        zip={zipCode}
        phone={phone}
        email={email}
        reviews={reviews}
      />
    </Layout>
  )
}

export default Locations
